import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const BaseTechnologies = (props) => (
    <div>
        <Helmet>
            <title>Base Technologies</title>
            <meta name="description" content="Base Technologies" />
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1>Base Technologies</h1>
                    </header>
                    <span className="image main"><img src={pic11} alt="" /></span>
                        <div className="row">
                            <div className="4u 12u$(medium)">
                                <h3>ReactJS</h3>
                                <p>React is a JavaScript library for building user interfaces. It is maintained by Facebook, Instagram and a community of individual developers and corporations.</p>
                            </div>
                            <div className="4u 12u$(medium)">
                                <h3>Gatsby</h3>
                                <p>Gatsby.js builds the fastest possible website. Instead of waiting to generate pages when requested, pre-build pages and lift them into a global cloud of servers — ready to be delivered instantly to your users wherever they are.</p>
                            </div>
                            <div className="4u 12u$(medium)">
                                <h3>Netlify</h3>
                                <p>Run your site on a purpose-built, multi-cloud infrastructure that’s designed for speed, automated to scale, and intrinsically secure.</p>
                            </div>
                        </div>
                        <ul className="pagination">
                            <li><Link to="loading_speeds"className="button small">Prev</Link></li>
                            <li><Link to="performance" className= "page">1</Link></li>
                            <li><Link to="multi_device" className="page">2</Link></li>
                            <li><Link to="loading_speeds" className="page">3</Link></li>
                            <li><Link to="#" className="page active">4</Link></li>
                            <li><Link to="clean_design" className="page">5</Link></li>
                            <li><Link to="products" className="page">6</Link></li>
                            <li><Link to="clean_design" className="button small">Next</Link></li>
                        </ul>
                </div>
            </section>
        </div>

    </div>
)

export default BaseTechnologies


